                <v-slide-x-transition>

                      <v-alert width="70%" v-if="errorDaftar" class="mx-auto  custom-chip" color="#F45050" dense>
                        <span style="color: white; font-size: 10px; font-weight: 500;">{{ errorDaftar }}</span>
