                </div>

              

                <v-form v-if="!isLoading" v-model="buttonDaftar" ref="formDataDiri" class="mx-6" @submit.prevent="submitForm">
                  <v-card-text class="py-0 mx-auto mt-8">
                    <div class="mb-6">
                      <h3 style="font-size: 20px; font-weight: 700;">Register</h3>
                      <h3 style="font-size: 12px; font-weight: 300;">Join Us.</h3>
                      

                    </div>
                    

                    <div>
                      <h4 class="custom-word" style="font-size: 10px!important; font-weight: 500; color: #8E8E8E;">Name</h4>

                      <v-text-field class="custom-label-style custom-details-style" single-line outlined id="name" name="name" v-model="name" label="Your Name (Ex: John Doe or John)" :rules="nameRules" required  color="#4682A9">
                        
                      </v-text-field>

                    </div>
                    <div>
                      <h4 class="custom-word" style="font-size: 10px!important; font-weight: 500; color: #8E8E8E ;">Email</h4>

                      <v-text-field class="custom-label-style" single-line outlined id="email" name="email" v-model="email" label="Your Email" :rules="emailRules" required  color="#4682A9"></v-text-field>
                    </div>

                    <div>
                      <h4 class="custom-word" style="font-size: 10px!important; font-weight: 500; color: #8E8E8E;">Password</h4>

                      <v-text-field
                        class="custom-label-style"
                        single-line outlined
                        id="password"
                        name="password"
                        v-model="password"
                        label="Your Password"
                        :rules="passwordRules"
                        required
                        
                        color="#4682A9"
                        type="password"
                      ></v-text-field>

                    </div>

                    <v-btn style="height: 32px;" rounded elevation="0" class="mt-9" block color="#4682A9" type="submit" :loading="loadingButton" :disabled="!buttonDaftar">
                      <span style="color: white; font-size: 8.5px; font-weight: 700;">Create</span>
                      <template v-slot:loader>
                        <v-progress-circular :width="4" :size="15" color="#91C8E4" indeterminate style="margin: 1rem;"></v-progress-circular>                      
                      </template>
