<script scoped>
  import axios from "axios";

  export default {
    data() {
      return {
        name: "",
        email: "",
        lastEmail: '',
        password: "",
        labelStyle: {
          "font-size": "16px", // Adjust the font size as needed
        },
        isLoading: true,
        loadingButton: false,
        
        formHasErrors: "",
        buttonDaftar: true,
        errorDaftar: null,
        successDaftar: null,
        nameRules: [(v) => !!v || "Name is required",   (v) => /^[A-Z][a-zA-Z]*(\s[A-Z][a-zA-Z]*)*$/.test(v) || "Ex: 'John Doe' or 'John'",],
        emailRules: [(v) => !!v || "Email is required", (v) => /.+@.+\..+/.test(v) || "Email must be valid"],
        passwordRules: [(v) => !!v || "Password is required", (v) => v.length >= 8 || "Password must be at least 8 characters"],
      };
    },

    created() {
      // Simulate a loading delay (replace this with your actual data loading logic)
      setTimeout(() => {
        this.isLoading = false; // Hide the loading indicator and show the card
      }, 1050); // Simulating a 2-second delay
    },

    computed: {
      appStyle() {
        return {
          background: 'linear-gradient(to right, #3085C3, #FAF2D3)',
          animation: 'rotateBackground 30s linear infinite',
          backgroundColor: "#eeeeee",
        };
      },
    },
    methods: {
      isSubmitDisabled() {
        if (this.$refs.formDataDiri.validate()) {
          //form is valid
          this.formHasErrors = false;
        } else {
          //form is not valid
          this.formHasErrors = true;
        }
      },

    
      async submitForm() {

        if (this.loadingButton) {
          return;
        }
        this.errorDaftar = null;
        this.successDaftar = null;
        this.loadingButton = true;

        await new Promise(resolve => setTimeout(resolve, 1500));


        const formData = new FormData();
        formData.append("name", this.name);
        formData.append("email", this.email);
        formData.append("password", this.password);

        try {
          const response = await axios.post("https://andrewoethara.my.id/database_register.php", formData);

          if (response.data.status === 'success') {
            this.successDaftar = "Register Succeded.";
            
          } else {
            this.errorDaftar = "Register Fail! Email not available.";
              console.log(response.data.message);

          }
        } catch (error) {
            console.log(error);

          
        } finally {
          this.loadingButton = false;
        }
      },


      navigateToNewPage() {
        // Navigate to the new page with a full page refresh
        window.location.href = "/";
      },
    },
  };
</script>

